  .button_container {
    margin: 30px auto;
    width: 150px;
    height: 40px;
    background-color: rgba(31, 31, 237, 0.942);
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .button {
    font-weight: 700;
    color: white;
    text-decoration: none;
    margin: 0 auto;
    transition: 0.2s;
  }
  
  .button_container:hover { background: rgb(40, 71, 224); }

  .button_container:active {
    background: rgb(20, 5, 233);
    }



  


