.figure {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ece7da;  
  position: absolute;
  margin: auto; 
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; 
}

.figure {
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }
  
  .figure:after {
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
  }

  .figure:after {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #facece;
    content: '';
    inset: 5px;
    mix-blend-mode: overlay;
    pointer-events: none;
  }

  .figureNumber {
    font-size: 2em;
    font-weight: 700;
    color: #333;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
  }

