  .selectRadius_container {
    margin: 10px auto;
  }

  .custom_slider {
    width: 100%;
    height: 8px;
    background: lightgray;
    border: 1px solid gray;
    border-radius: 10px;
    opacity: 0.7;
    outline: none;
    transition: 0.3s;
    -webkit-appearance: none;
  }
  
  .custom_slider:hover {
    opacity: 1;
  }
  
  .custom_slider::-webkit-slider-thumb {
    appearance: none;
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    background: #270dd2;
    border-radius: 50%;
    cursor: pointer;
  }
  
  


